@import url(https://fonts.google.com/specimen/Open+Sans?query=open+sa#standard-styles);
html {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
    font-family: 'Montserrat',Arial,sans-serif;
    display: flex;
    padding: 0px 15px;
    justify-content: space-between;
    position: -webkit-sticky;
    position: sticky;
    background: linear-gradient(170deg, rgb(249, 248, 253) 50%, rgb(230, 232, 253) 60%);
    z-index: 100;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
}

.header-right {
    padding: 10px 15px;
}
.spacer {
    flex: 1 1;
}

.c-nav ul {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
}

.c-nav a {
    display: block;
    padding: 16px 8px;
    font-size: 20px;
    font-weight: bold;
}

.c-nav a:link {
    text-decoration: none;
    align-items: center;
    padding: 20px;
}

.c-nav a:hover {
    /* text-decoration-color: rgb(82, 54, 84); */
    /* text-decoration: none; */
    background-color: #eff2f5;
    border-radius: 10px;
    align-items: center;
    padding: 20px;

}

.c-nav a:active {
    color: #e52a6f;
    /* text-decoration: none; */
    border-bottom: 3px solid  #e52a6f;
    ;

}

.c-nav li {
    display: inline-block;
}

.header-left {
    display: flex;
    padding: 10px 20px;
    margin-left: 1rem;
}

.header-left img {
    display: block;

}

.header-left h4 {
    display: block;
    color: rgb(139, 36, 23);
    text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
    font-size: 20px;
    margin-left: 10px;
}

@media(max-width: 768px) {
    .c-nav {
        display: none;
    }

}

@media(min-width: 769px) {
    .header-left {
        margin-left: 0;
    }
}
.toggle-button {
    height: 80px;
    width: 32px;
    background: transparent;
    border: none;
    display: flex;
    flex-direction: column;
    padding: 15px 1px;
    box-sizing: border-box;
    justify-content: space-between;    
}

.toggle-button:focus {
    outline: none;
}

.toggle-button_line {
    width: 100%;
    height: 3px;
    background: rgb(139, 36, 23);
}

@media(min-width: 769px) {
    .toggle-button  {
        display: none;
    }
}
.side-drawer {
    height: 100%;
    background: linear-gradient(rgba(64,95,174,0.8) 30%, rgba(61, 80, 156, 0.7) 50%, rgba(96, 128, 207, 0.7) 80%);
    box-shadow: 2px 1px 2px rgba(0, 0, 0, 0.4);
    position: fixed;
    top:83;
    left:0;
    width: 75%;
    max-width: 400px;
    z-index: 200;
    transition: transform 0.1s ease-in;
    transform: translateX(-100%);
}

.side-drawer.open-drawer {
    transform: translateX(0);

}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 30px;
    padding: 10px 40px;
}

.side-drawer li {
    margin: 1rem;
    font-family: 'open sans';
    font-weight: bolder;
}

.side-drawer a {

    color: white;
    font-weight: bold;
    text-decoration: none;
    font-size: 1.1rem;
}

.side-drawer a:hover {
    background-color: rgba(255, 255, 255, 0.2);
    padding: 10px 10px;
}

.side-drawer a:active {
    background-color: rgba(255, 255, 255, 0.2);
    padding: 10px 10px;
} 

@media(min-width: 769px) {
    .side-drawer {
        display: none;
    }
}
.backdrop {
    position: fixed;
    top:83;
    left:0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.4);
    z-index: 100;
}
